import React from "react";
import Container from "./../Container";
import Link from "next/link";

import * as style from "./Menu.module.scss";

function Menu() {
  return (
    <Container>
      <ul className="menu">
        <li className="menu__item">
          <Link href="/">
            <a>О нас</a>
          </Link>
        </li>
        <li className="menu__item">Автовыкуп в городах</li>
        <li className="menu__item">Полезная Информация</li>
        <li className="menu__item">
          <Link href="/contact">
            <a className={style.menu__item__link}>Контакты</a>
          </Link>
        </li>
      </ul>
    </Container>
  );
}
/*
О нас
Автовыкуп в городах
    выпадающий список городов
Полезная Информация
Контакты




*/
export default Menu;
